<script>
import qs from 'qs'
import boyTeacher from '@/assets/img/boy_teacher.jpg'
import { mapState } from 'vuex'
export default {
  name: 'Chatroom',
  // props: {
  //   readonly: {
  //     type: Boolean,
  //     default: false
  //   },
  //   selectTimeDefault: {
  //     type: String,
  //     default: null
  //   },
  //   selectDateDefault: {
  //     type: Array,
  //     default: null
  //   }
  // },
  data: function () {
    return {
      boyTeacher,
      websock: null,
      messageText: ''
    }
  },
  // created () {
  //   this.initWebSocket()
  // },
  // destroyed () {
  //   this.websock.close()
  // },
  computed: {
    ...mapState(['loginStatus', 'doChatView', 'doChatAction', 'doChatroomID']),
    userInfo () {
      return JSON.parse(localStorage.getItem('user'))
    }
  },
  watch: {
    loginStatus (val) {
      // if (val) {
      //   this.getHistoricalMessage()
      //   this.initWebSocket()
      // } else {
      //   alert('已手動斷線')
      //   this.websock.close()
      //   this.$router.go(0)
      // }
    },
    doChatView (val) {
      if (val) {
        this.doOpenChatWindows()
      }
    }
  },
  mounted () {
    if (localStorage.getItem('token')) {
      this.getHistoricalMessage()
      this.initWebSocket()
    } else {
      alert('已手動斷線')
      this.websock.close()
      this.$router.go(0)
    }
  },
  methods: {
    getHistoricalMessage () {
      alert('取得歷史訊息')
      if (localStorage.getItem('token')) {
        alert('取得中')
        let body = {
          userID: this.userInfo ? this.userInfo.user_id : -1,
          user_type: this.userInfo.type
        }
        this.$http.post('/api/chat/contentAndHistoryOfChatroom/', qs.stringify(body)).then(res => res.data)
          .then(res => {
            if (res.status === 'success') {
              // this.snackbar = true
              // this.color = 'green'
              // this.errorMessages = '已開啟聊天室'
              alert('已取得歷史訊息')
            } else {
              this.snackbar = true
              this.color = 'red'
              this.errorMessages = res.errMsg
            }
          })
      } else {
        this.$store.state.dologinView = true
      }
    },
    initWebSocket () {
      const wsuri = 'ws://127.0.0.1:8000/ws/chat/1_chatroom_1/'
      this.websock = new WebSocket(wsuri)
      this.websock.onmessage = this.websocketonmessage
      this.websock.onopen = this.websocketonopen
      this.websock.onerror = this.websocketonerror
      this.websock.onclose = this.websocketclose
      console.log(this.websock)
    },
    websocketonerror () { // 连接建立失败重连
      // this.initWebSocket()
    },
    websocketonopen () { // 连接建立之后执行send方法发送数据
      alert('已連線')
    },
    websocketonmessage (e) { // 数据接收
      const redata = JSON.parse(e.data)
      console.log(e.data)
      console.log(redata)
    },
    websocketsend () { // 数据发送
      let msg = {
        userID: 1,
        chatID: 1,
        messageType: 0,
        messageText: this.messageText,
        message: '舊版-' + this.messageText
      }
      this.websock.send(JSON.stringify(msg))
      alert('已發送')
    },
    websocketclose () {
      alert('已自動斷線')
    },
    doOpenChatWindows () {
      if (this.$store.state.doChatAction === 'message') {
        alert('已開啟與' + this.$store.state.doChatroomID + '的聊天室')
      }
    },
    doCloseChatWindows () {
      this.$store.state.doChatView = false
    }
  }
}
</script>
