  computed: {
    ...mapState('app', ['theme']),
    pages () {
      return this.pagination.rowsPerPage ? Math.ceil(this.items.length / this.pagination.rowsPerPage) : 0
    },
    pageslist () {
      let i = 0
      let list = []
      if (this.pagination.rowsPerPage) {
        for (i = 0; i < this.items.length; i++) {
          list.push(i + 1)
        }
      }
      return list
    },
    options_bar () {
      return {
        data: {
          columns: [
            ['Project A', 0, 20, 10, 40, 15, 35],
            ['Project B', 30, 200, 100, 400, 150, 250]
          ],
          type: 'bar'
        },
        axis: {
          x: {
            label: {
              text: '期',
              position: 'outer-center'
            }
          },
          y: {
            label: {
              text: '億',
              position: 'outer-middle'
            }
          }
        },
        tooltip: {
          format: {
            title (d) {
              return `第 ${d} 期`
            },
            value (value, ratio, id) {
              return `${value} 億`
            }
          }
        },
        grid: {
          x: {
            show: true
          },
          y: {
            show: true
          }
        },
        legend: {
          position: 'bottom',
          padding: 16
        },
        size: {
          // width: 640,
          height: 500
        }
      }
    },
    options_line () {
      return {
        data: {
          columns: [
            ['Project A', 0, 20, 10, 40, 15, 35],
            ['Project B', 30, 200, 100, 400, 150, 250]
          ]
        },
        point: {
          r: 10
        },
        axis: {
          x: {
            label: {
              text: '期',
              position: 'outer-center'
            },
            padding: 0.25
          },
          y: {
            label: {
              text: '億',
              position: 'outer-middle'
            }
          }
        },
        tooltip: {
          format: {
            title (d) {
              return `第 ${d} 期`
            },
            value (value, ratio, id) {
              return `${value} 億`
            }
          }
        },
        grid: {
          x: {
            show: true
          },
          y: {
            show: true
          }
        },
        legend: {
          position: 'right',
          padding: 16
        },
        size: {
