  watch: {
    d1 (val) {
      this.menu1 = false
      if (this.d2 <= this.d1) {
        this.d2 = this.d1
      }
    },
    d2 (val) {
      this.menu2 = false
    }
  },
  mounted () {
    this.handler_bar.$emit('init', this.options_bar)
    this.handler_line.$emit('init', this.options_line)
    this.handler_mix.$emit('init', this.options_mix)
    this.handler_pie.$emit('init', this.options_pie)
  },
  methods: {
    shrink1 () {
      if (this.shrink_1) {
        this.shrink_1 = false
      } else {
        this.shrink_1 = true
      }
    },
    shrink2 () {
      if (this.shrink_2) {
        this.shrink_2 = false
      } else {
        this.shrink_2 = true
      }
    },
    shrink3 () {
      if (this.shrink_3) {
        this.shrink_3 = false
      } else {
        this.shrink_3 = true
      }
    },
    shrink4 () {
      if (this.shrink_4) {
        this.shrink_4 = false
      } else {
        this.shrink_4 = true
      }
    },
    beforeSubmit () {
      let err = []
      if (this.d1 !== '' && this.d1 !== null) {
        if (!/^[0-9]{4}[-][0-9]{2}[-][0-9]{2}/g.test(this.d1) || this.d1.length !== 10) {
          err.push('日期1格式錯誤')
        }
      }
      if (this.d2 !== '' && this.d2 !== null) {
        if (!/^[0-9]{4}[-][0-9]{2}[-][0-9]{2}/g.test(this.d2) || this.d2.length !== 10) {
          err.push('日期2格式錯誤')
        }
      }
      return err
    },
    doSubmit (event) {
      this.error_info = []
      const err = this.beforeSubmit()
      if (err.length > 0) {
      // this.$notify({
      //   group: "post",
      //   title: this.$t('sys.system_notification'),
      //   text: err.join(","),
      //   type: "error"
      // });
      // return;
        this.error_info = err.join(',')
        this.snackbar = true
        return
      }
      this.no_data = true
    }
  }
}
